.wrapper {
 display: grid;
 grid-template-columns: 1fr;
 grid-template-rows: auto;
 grid-template-areas:
 'header'
 'FoodHub'
}

.header {
 grid-area: header;
 background: #1995AD;
 color: #BCBABE;
 position: fixed;
 top: 0;
 width: 100%;
 z-index: 99;
}

 @tailwind preflight;

